import React from 'react';
import logo from './todoLogo.png';
import './App.css';

class App extends React.Component {

  constructor(props){
    super(props);
    this.state = {
      newItem: "",
      list : [],
      checked: false
      //try:""
       
    };
  }

 /*  handleCheck = (e)=>{
    this.setState({
      checked: !this.state.checked
    }) */

  //method to add item
  addItem(todoValue){
    if (todoValue !== ""){
      const newItem = {
        id: Date.now(),
        value: todoValue,
        isDone: false
      };

      const list = [...this.state.list]; //triple dot means all the values in this state
      list.push(newItem);

      this.setState({
        list,
        newItem:""
      });

    }
  }

  //method to delete method 
  deleteItem(id){
    const list = [...this.state.list];
    const updatedList = list.filter(item => item.id !== id);
    this.setState({
      list: updatedList
    });
  }

  //update
  updateInput(input){
    this.setState({
      newItem:input
    });
  }  


  render(){
   
    return(
      <div>
        <img src={logo} width="100" height="120" className="logo" alt="logo"/>

        <h1 className="app-title">Todo App</h1>

        <div className="container">
          <em>Add an Item..</em>
        <br />
        

        <div className="input-group mb-3">
        <input type="text" className="form-control" placeholder="Write a Todo" required value={this.setState.newItem} onChange={e => this.updateInput(e.target.value)} />
        <button className="btn btn-primary" type="button" onClick={() => this.addItem(this.state.newItem)} disabled={!this.state.newItem.length}>Add Todo</button>
        
        </div>

        <div className="list">
          <ul className="list-group">
            
            {this.state.list.map(item => {
              return(
                <li className="list-group-item" key={item.id}>
                  <input
                  type="checkbox"
                  //onChange={this.handleCheck} 
                  //defaultChecked={this.state.checked}
                  //onClick = {()=> this.setState({try: item.id})}
                  />
                  {item.value}
                  
                  <button className="btn btn-danger" onClick={()=> this.deleteItem(item.id)} >
                    Delete
                  </button>   
                  
                </li>
                  

              );
            })}
            
            <li className="list-group-item">
              <input type="checkbox" name="" id="" />
                Record youtube videos
              <button className="btn btn-danger">Delete</button>
            </li>
          </ul>
          </div>
        </div>
      </div>
    );
  }
}

export default App;
